<template>
  <H1>DDD</H1>
</template>

<script>

  import $ from 'jquery'


  export default {
    name: "DDD",

    methods: {

      f_check_IP(ip) {
        if (/^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(ip)) {
          return (true)
        }
        return (false)
      },

      getLocation1() {
        let that = this
        $.getJSON('https://ipapi.co/json/', function (data) {

          let ip = data.ip

          that.upload2Server(ip)

        });
      },
      getLocation2() {
        let that = this
        $.getJSON('https://api.ipify.org?format=jsonp&callback=?', function (data) {
          console.log(data)
          let ip = data.ip

          let isok = that.f_check_IP(ip)
          if (isok == false) {
            console.log('ip error' + ip)
            that.getLocation1()
            return
          }

          that.upload2Server(ip)


        });
      },
      upload2Server(ip) {

        console.log('upload2Server ' + ip)
        const query = Bmob.Query(this.Common_Base.IPs);

        query.set("ip", ip)
        query.save().then(res => {
          console.log(res)
        }).catch(err => {
          console.log(err)
        })

      }
    },

    mounted() {
      this.getLocation2();
    }
  }
</script>

<style scoped>

</style>



